import React from 'react';
import JsBarcode from 'jsbarcode';
import { Page, Image, Text, Document, StyleSheet  } from '@react-pdf/renderer';
import { size } from 'lodash';
import { volume } from 'business/utils/number';
import { clientF } from 'portal/services/client.service';

const heading = {
  top: 0,
  left: 0,
};

const textAbs = (top, left, fontSize, width) => ({ top, left, fontSize, width: width, border: 0, position: 'absolute' })
const lineAbs = (top) => ({ top, borderBottom: 1, width: 290, position: 'absolute' })

const styles = StyleSheet.create({
  barcode: {
    position: 'absolute',
    top: 450,
    left: 20,
    height: 120, 
    width: 250,
  },
  logo: {
    position: 'absolute',
    top: 30,
    left: 20,
    height: 38, 
    width: 42,
  },
  
  date:
    textAbs(heading.top + 10, heading.left + 10, 8),
  hotline:
    textAbs(heading.top + 10, heading.left + 115, 8),
  website:
    textAbs(heading.top + 10, heading.left + 225, 8),

  packageCountStr:
    textAbs(heading.top + 28, heading.left + 225, 7),
  packageCount:
    textAbs(heading.top + 35, heading.left + 225, 24),
  cargoMode:
    textAbs(heading.top + 32, heading.left + 105, 13),
  deliveryType:
    textAbs(heading.top + 45, heading.left + 105, 13),
  
  from:
    textAbs(heading.top + 72, heading.left + 10, 8),
  name:
    textAbs(heading.top + 80, heading.left + 10, 10),
  name2:
    textAbs(heading.top + 90, heading.left + 10, 10),
  address:
    textAbs(heading.top + 102, heading.left + 10, 10),
  address2:
    textAbs(heading.top + 112, heading.left + 10, 10),
  contact:
    textAbs(heading.top + 124, heading.left + 10, 10),

  to:
    textAbs(heading.top + 152, heading.left + 10, 8),
  slName:
    textAbs(heading.top + 160, heading.left + 10, 10),
  slName2:
    textAbs(heading.top + 170, heading.left + 10, 10),
  slAddress:
    textAbs(heading.top + 182, heading.left + 10, 10),
  slAddress2:
    textAbs(heading.top + 192, heading.left + 10, 10),
  slContact:
    textAbs(heading.top + 204, heading.left + 10, 10),
  
  originText:
    textAbs(heading.top + 232, heading.left + 10, 8),
  destinationText:
    textAbs(heading.top + 232, heading.left + 150, 8),
  origin:
    textAbs(heading.top + 240, heading.left + 10, 18),
  destination:
    textAbs(heading.top + 240, heading.left + 150, 18),
  
  weightText:
    textAbs(heading.top + 287, heading.left + 10, 8),
  volumeText:
    textAbs(heading.top + 287, heading.left + 150, 8),
  weight:
    textAbs(heading.top + 295, heading.left + 10, 14),
  volume:
    textAbs(heading.top + 295, heading.left + 150, 14),

  hblText:
    textAbs(heading.top + 322, heading.left + 10, 8),
  packPid:
    textAbs(heading.top + 322, heading.left + 150, 8),
  packType:
    textAbs(heading.top + 330, heading.left + 150, 14),
  mhblText:
    textAbs(heading.top + 370, heading.left + 10, 8),
  hbl:
    textAbs(heading.top + 330, heading.left + 10, 30),
  
});

const UI = (props) => {
  const { cargo, packages } = props;
  
  if (!cargo) return <Document></Document>;

  const barcodes = []
  packages.forEach(p => {
    let canvas;
    canvas = document.createElement('canvas');
    JsBarcode(canvas, cargo.hbl + '|' + p.id, {
      displayValue: false,
    });
    const barcode = canvas.toDataURL();
    barcodes.push(barcode);
  })

  const d = clientF(cargo.branch);

  return (<Document>
    {packages.map((i, j) => <Page size={{width: '102mm', height: '203mm'}}>
        <Text style={styles.date}>Date: {cargo.pickedUpDate || cargo.pickupDate} {cargo.pickedUpTime || cargo.pickupTime}</Text>
        <Text style={styles.hotline}>{d.phone}</Text>
        <Text style={styles.website}>{d.website}</Text>
        <Text style={lineAbs(0)}>{'  '}</Text>
        
        <Image src={"/app/images/" + d.parent + "/" + cargo.branch + "/logo_client.jpg"} style={styles.logo} /> 
        <Text style={styles.packageCountStr}>{'PACKAGES'}</Text>
        <Text style={styles.packageCount}>{j + 1}/{size(packages)}</Text>
        <Text style={styles.cargoMode}>{cargo.cargoMode}</Text>
        <Text style={styles.deliveryType}>{cargo.deliveryType}</Text>
        <Text style={lineAbs(50)}>{'  '}</Text>
        
        <Text style={styles.from}>{'FROM:'}</Text>
        <Text style={styles.name}>{cargo.name?.substring(0, 45)}</Text>
        <Text style={styles.name2}>{cargo.name?.substring(45)}</Text>
        <Text style={styles.address}>{cargo.address?.substring(0, 45)}</Text>
        <Text style={styles.address2}>{cargo.address?.substring(45)}</Text>
        <Text style={styles.contact}>{cargo.contact}</Text>
        <Text style={lineAbs(120)}>{'  '}</Text>
        
        <Text style={styles.to}>{'TO:'}</Text>
        <Text style={styles.slName}>{cargo.slName?.substring(0, 45)}</Text>
        <Text style={styles.slName2}>{cargo.slName?.substring(45)}</Text>
        <Text style={styles.slAddress}>{cargo.slAddress?.substring(0, 45)}</Text>
        <Text style={styles.slAddress2}>{cargo.slAddress?.substring(45)}</Text>
        <Text style={styles.slContact}>{cargo.slContact}</Text>
        <Text style={lineAbs(200)}>{'  '}</Text>

        <Text style={styles.originText}>{'ORIGIN:'}</Text>
        <Text style={styles.origin}>{'DOH'}</Text>
        <Text style={styles.destinationText}>{'DESTINATION:'}</Text>
        <Text style={styles.destination}>{'CMB'}</Text>
        <Text style={lineAbs(260)}>{'  '}</Text>
        
        <Text style={styles.weightText}>{'WEIGHT:'}</Text>
        <Text style={styles.weight}>{i.weight || '-'}</Text>
        <Text style={styles.volumeText}>{'VOLUME:'}</Text>
        <Text style={styles.volume}>{volume(i)}</Text>
        <Text style={lineAbs(295)}>{'  '}</Text>
        
        <Text style={styles.hblText}>{'HBL:'}</Text>
        <Text style={styles.hbl}>{cargo.hbl}</Text>
        <Text style={styles.packPid}>PID: {i.id}</Text>
        <Text style={styles.packType}>{i.type}</Text>
        <Text style={styles.mhblText}>{'MHBL:'}</Text>
        <Text style={lineAbs(410)}>{'  '}</Text>
        
        <Image src={barcodes[j]} style={styles.barcode} /> 
    </Page>)}
  </Document>);
}

export default UI;
