import React from 'react';
import { Page, Image, Text, Document, StyleSheet  } from '@react-pdf/renderer';
import numToWord from 'business/utils/numToWord';
import { clientF } from 'portal/services/client.service';

const heading = {
  top: 125,
  left: 20,
  size: 10  
};

const textAbs = (top, left, fontSize, width) => ({ top, left, fontSize, width: width, border: 0, position: 'absolute' })

const styles = StyleSheet.create({
  pageBackground: {
    position: 'absolute', display: 'block',
    height: 590 * 1449 / 1042, // dimesnsion of the image
    width: 590,
  },

  hbl:
    textAbs(heading.top + 0, heading.left + 355, heading.size),
  date:
    textAbs(heading.top + 12, heading.left + 355, heading.size),
  time:
    textAbs(heading.top + 24, heading.left + 355, heading.size),
  driver:
    textAbs(heading.top + 36, heading.left + 355, heading.size),
  
  name:
    textAbs(heading.top + 12, heading.left + 27, heading.size),
  name2:
    textAbs(heading.top + 24, heading.left + 27, heading.size),
  address:
    textAbs(heading.top + 40, heading.left + 27, heading.size),
  address2:
    textAbs(heading.top + 52, heading.left + 27, heading.size),
  
  row1: textAbs(heading.top + 130, heading.left + 30, heading.size),
  row2: textAbs(heading.top + 130, heading.left + 155, heading.size),
  row3: textAbs(heading.top + 130, heading.left + 290, heading.size),
  row4: textAbs(heading.top + 130, heading.left + 455, heading.size),

  total:
    textAbs(heading.top + 403, heading.left + 455, heading.size - 1),
  discount:
    textAbs(heading.top + 413, heading.left + 455, heading.size - 1),
  net:
    textAbs(heading.top + 426, heading.left + 455, heading.size - 1),
  amountInWords:
    textAbs(heading.top + 442, heading.left + 110, heading.size - 1),
  remarks:
    textAbs(heading.top + 480, heading.left + 40, heading.size - 1),
  cash:
    textAbs(heading.top + 532, heading.left + 220, heading.size - 1),
  finalTotal:
    textAbs(heading.top + 573, heading.left + 220, heading.size - 1),
});

const UI = (props) => {
  const { cargo } = props;
  
  if (!cargo) return <Document></Document>;
  const d = clientF(cargo.branch);

  return (<Document>
    <Page size="A4" orientation="portrait">
        <Image src={"/app/images/" + d.parent + "/" + cargo.branch + "/cr.jpg"} style={styles.pageBackground} />
        <Text style={styles.hbl}>{cargo.hbl + 2777}</Text>
        <Text style={styles.date}>{cargo.pickedUpDate || cargo.pickupDate}</Text>
        <Text style={styles.time}>{cargo.pickedUpTime || cargo.pickupTime}</Text>
        <Text style={styles.driver}>{cargo.auth || '-'}</Text>

        <Text style={styles.name}>{cargo.name?.substring(0, 42) || '-'}</Text>
        <Text style={styles.name2}>{cargo.name?.substring(42)}</Text>
        <Text style={styles.address}>{cargo.address?.substring(0, 42) || '-'}</Text>
        <Text style={styles.address2}>{cargo.address?.substring(42)}</Text>
        
        <Text style={styles.row1}>{'1'}</Text>
        <Text style={styles.row2}>{cargo.hbl}</Text>
        <Text style={styles.row3}>{cargo.cargoMode + '\\' + cargo.deliveryType}</Text>
        <Text style={styles.row4}>{cargo.paid + ' ' + cargo.currency}</Text>
        
        <Text style={styles.total}>{cargo.paid + ' ' + cargo.currency}</Text>
        <Text style={styles.discount}>{'-'}</Text>
        <Text style={styles.net}>{cargo.paid + ' ' + cargo.currency}</Text>

        <Text style={styles.amountInWords}>{numToWord(cargo.paid) + ' only' || '-'}</Text>
        <Text style={styles.remarks}>{cargo.remarks || '-'}</Text>

        <Text style={styles.cash}>{'cash'}</Text>
        <Text style={styles.finalTotal}>{cargo.paid + ' ' + cargo.currency}</Text>

    </Page>
  </Document>);
}

export default UI;
