import React from 'react';
import { useMediaQuery, Grid, Typography, Button } from '@material-ui/core';
import { Heading } from 'business/widgets/Mini';
import LocalAtm from '@material-ui/icons/LocalAtm';
import Assignment from '@material-ui/icons/Assignment';
import Print from '@material-ui/icons/Print';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import Service from 'business/services/members.service';
import palette from 'portal/theme/palette';
import { useSnackbar } from 'notistack';
import { PDFDownloadLink, PDFViewer } from '@react-pdf/renderer';
import PrinterHBL from './printHbl';
import PrinterCR from './printCr';
import PrinterBar from './printBarcode';
import ServiceClient, { clientX } from 'portal/services/client.service';
import { ViewWeek } from '@material-ui/icons';
import QRCode from "qrcode.react";

const tabs = ['MEMBERSHIP', 'CR', 'BAR']

const UI = (props) => {
    const { enqueueSnackbar } = useSnackbar();
    const id = props.match?.params?.id || props.id;
    const branch = props.match?.params?.branch || props.branch;
    const tab = +(props.match?.params?.tab || 1) - 1;
    const isPrint = useMediaQuery('print')
    const [format, setFormat] = React.useState(tabs[tab]);
    const [details, setDetails] = React.useState({});
    const [loading, setLoading] = React.useState(false);

    React.useEffect(() => {
        // debugger
        setLoading(true)
        Service.get({ id })
            .then(rows => {
                // if (!(clientX().branch === rows.cargo.branch || clientX().branch === rows.cargo.destination)) {
                //     enqueueSnackbar('You are not Authorized', { variant: 'warning' });
                //     return;
                // }
                debugger
                setDetails(rows);
            }).catch(e => {
                debugger
                enqueueSnackbar('Could not fetch', { variant: 'error' });
            }).finally(e => setLoading(false))
    }, [enqueueSnackbar, id, branch]);

    return (<Grid>
        {!isPrint && <Grid container spacing={1} justify="space-between" alignContent="space-between" alignItems="space-between" style={{padding: 20, marginBottom: 20, background: palette.shades.light }}>
            {/* <Heading size={4} title={'Print Membership'} subtitle={'Zahira College ' + process.env.REACT_APP_OLD_TYPE} /> */}
            {/* <div style={{display: 'none'}}>
                // {details.cargo?.name && <QRCode id="qrcode-mw" value={details.cargo?.name + '\n' + details.cargo?.contact + '\n' + details.cargo?.qid} size={200} level={"H"} includeMargin={false} />}
            </div> */}
            <Grid container spacing={2} xs={8} justify="flex-end" alignContent="center" alignItems="center" style={{paddingRight: 10, paddingTop: 10}}>
                <ToggleButtonGroup value={format} onChange={(e, v) => setFormat(v)} exclusive>
                    <ToggleButton value={tabs[0]} style={format === tabs[0] ? {background: palette.shades.green} : {}}>
                        <Assignment />
                        <Typography>&nbsp; Membership</Typography>
                    </ToggleButton>
                    {/* <ToggleButton value={tabs[1]} style={format === tabs[1] ? {background: palette.shades.green} : {}}>
                        <LocalAtm />
                        <Typography>&nbsp; Cash Reciept</Typography>
                    </ToggleButton>
                    <ToggleButton value={tabs[2]} style={format === tabs[2] ? {background: palette.shades.green} : {}}>
                        <ViewWeek />
                        <Typography>&nbsp; Barcode</Typography>
                    </ToggleButton> */}
                </ToggleButtonGroup>
                <div>
                    {!loading && details.uuid && <PDFDownloadLink document={
                        format === tabs[0]
                            ? <PrinterHBL all={details || {}} />
                            : format === tabs[1]
                                ? <PrinterCR cargo={details.cargo} />
                                : <PrinterBar cargo={details.cargo} packages={details.packages} />
                        } fileName={format + "_" + details.uuid + "_" + details.shortName +".pdf"}>
                        {({ blob, url, loading, error }) => {
                            console.log('file error', error)
                            // console.log(url)
                            return loading ? 'Loading...' : 
                                <Button startIcon={<Print />} color='primary' variant="contained" size="large" style={{marginLeft: 10}}>Print PDF</Button>
                        }}
                    </PDFDownloadLink>}
                </div>
            </Grid>
        </Grid>}
        {!loading && details.uuid && <Grid style={{padding: 30}}>
            <PDFViewer width={1100} height={800}> 
                {format === tabs[0]
                    ? <PrinterHBL all={details} />
                    : format === tabs[2]
                        ? <PrinterBar all={details.cargo} packages={details.packages} />
                        : <PrinterCR cargo={details.cargo} />
                }
            </PDFViewer>
        </Grid>}
    </Grid>);
};

export default UI;
